import React, { useState } from 'react';
import Select from 'react-select';
import './Navbar.css';
import Heart from 'react-heart';

const Navbar = ({ userLang, setUserLang, userTheme,
	setUserTheme, fontSize, setFontSize}) => {
	const languages = [
		{ value: "c", label: "C" },
		{ value: "cpp", label: "C++" },
		{ value: "python", label: "Python" },
		{ value: "java", label: "Java" },
	];
	const themes = [
		{ value: "vs-dark", label: "Dark" },
		{ value: "light", label: "Light" },
	]
	const customStyles = {
		option: (provided, state) => ({
		  ...provided,
		  color: state.isSelected ? '#fff' : '#000', // Change text color when selected
		}),
	  };
	  const [active, setActive] = useState(true) 
	return (
		<div className="navbar">
			<h1>Compiler</h1>
			<Select options={languages} value={userLang}
				onChange={(e) => {
					setUserLang(e.value)
					
				}}
				placeholder={userLang}  styles={customStyles}  />
			<Select options={themes} value={userTheme}
				onChange={(e) => setUserTheme(e.value)}
				placeholder={userTheme} styles={customStyles} />
			<label>Font Size</label>
			<input type="range" min="14" max="30"
				value={fontSize} step="2"
				onChange={(e) => { setFontSize(e.target.value) }} />
			<h2 style={{marginLeft:'23%'}}>Made By RVCODER</h2>
			<div style={{ width: "2rem" }}>
			<Heart isActive={active} onClick={() => setActive(!active)}/> 
			</div>
		</div>
	)
}

export default Navbar
